.popup{
    position: fixed;
    display: flex;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0,0,0, 0.5);
    visibility: hidden;
    justify-content: center;
    align-items: center;
    transition: visibility 0.5s, opacity .5s linear;
    opacity: 0;
}